.back-btn {
	height: calc(2.5vh + 2.5vw);
    width: calc(2.5vh + 2.5vw);
    background-image: url('../img/icons/back-btn.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 50%;
}

.confirm-btn {
	height: calc(2.5vh + 2.5vw);
    width: calc(2.5vh + 2.5vw);
    background-image: url('../img/icons/confirm-btn.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 50%;
}



